export const getUrl = (title) =>
  title
    .toLowerCase()
    .replace(/ /g, '-')
    .replace(/[,]/g, '')
    .replace(/[]/g, 'a')
    .replace(/[]/g, 'e')
    .replace(/[]/g, 'i')
    .replace(/[]/g, 'o')
    .replace(/[]/g, 'u')
    .replace(/[]/g, 'c')
    .replace(/[]/g, 'd')
    .replace(/[]/g, 's')
    .replace(/[]/g, 'z');
