export const getCroatianMonthName = (hungarianName) => {
  switch (hungarianName) {
    case 'janur':
      return 'sijenja';
    case 'februr':
      return 'veljae';
    case 'mrcius':
      return 'oujka';
    case 'prilis':
      return 'travnja';
    case 'mjus':
      return 'svibnja';
    case 'jnius':
      return 'lipnja';
    case 'jlius':
      return 'srpnja';
    case 'augusztus':
      return 'kolovoza';
    case 'szeptember':
      return 'rujna';
    case 'oktber':
      return 'listopada';
    case 'november':
      return 'studenoga';
    case 'december':
      return 'prosinca';
    default:
      return '';
  }
};
